@import "https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,400;0,700;1,300&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
}

p {
  padding: 0;
}

body {
  height: 100vh;
  background-image: url("bg.9c2eeda7.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Kanit, sans-serif;
  display: flex;
  position: relative;
  overflow: hidden;
}

.loader {
  color: #fff;
  z-index: 12;
  font-size: 60px;
  position: absolute;
}

.breed-select {
  z-index: 10;
  height: 50px;
  width: 250px;
  color: #000;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .8);
  border-radius: 10px;
  padding: 10px;
  font-family: Kanit, sans-serif;
  font-size: 20px;
  font-weight: 700;
  transition: all .5s linear;
  position: absolute;
  top: 5%;
  left: 1%;
}

.show-dogs {
  z-index: 10;
  height: 50px;
  width: 150px;
  color: #000;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .8);
  border-radius: 10px;
  padding: 10px;
  font-family: Kanit, sans-serif;
  font-size: 20px;
  font-weight: 700;
  transition: all .5s linear;
  position: absolute;
  top: 5%;
  left: 18%;
}

.breed-select:hover, .show-dogs:hover {
  background-color: #fff;
}

.cat-card {
  max-width: 600px;
  max-height: 800px;
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  background-color: rgba(255, 255, 255, .2);
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.cat-img {
  width: 200px;
  max-height: 250px;
  height: auto;
  object-fit: cover;
  border-radius: 20px;
  transition: all .3s;
  transform: scale(.95);
}

.cat-card:hover .cat-img {
  transform: scale(1);
}

.cat-name {
  letter-spacing: 5px;
  font-size: 30px;
  font-weight: 700;
  font-style: bold;
}

.cat-description {
  font-size: 18px;
}

.cat-temperament {
  font-size: 20px;
  font-style: italic;
  font-weight: 300;
}

.cat-temperament-title {
  font-style: normal;
}

.is-hidden {
  visibility: hidden;
}

